.sedona-map {
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
   height: 350px;
   &__map {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      img {
         width: 100%;
         height: 100%;
      }
   }

   &__icon {
      position: relative;
      z-index: 2;
   }
}
