.header {
   height: 56px;
   transition: all 0.3s ease 0s;
   &--active {
      margin-top: 220px;
   }
   &__container {
      position: relative;
      padding: 0;
   }

   &__icon {
      display: inline-block;
      width: 22px;
      height: 23px;
      background: none;
      border: none;
      margin: 0;
      padding: 0;
      position: absolute;
      right: 20px;
      top: 17px;
      &--active {
         z-index: 5;
         top: -200px;
      }
      @media (min-width: $width-tablet+px) {
         display: none;
      }
      .header__icon--burger {
         opacity: 1;
         visibility: visible;
         fill: $color_main;
      }
      &--active .header__icon--burger {
         opacity: 0;
         visibility: hidden;
      }
      .header__icon--cross {
         opacity: 0;
         visibility: hidden;
         fill: $color_white;
      }
      &--active .header__icon--cross {
         opacity: 1;
         visibility: visible;
      }
   }

   &__logo {
      display: inline-block;
      position: absolute;
      z-index: 5;
      top: 0;
      left: 50%;
      transform: translate(-50%);
      @media (min-width: 1200px) {
         img {
            width: 186.6px;
            height: 96.43px;
         }
      }
   }

   &__menu {
   }
}
.header-menu {
   position: relative;
   display: none;
   &--active {
      display: block;
      position: absolute;
      top: -220px;
      left: 0;
      right: 0;
      width: 100%;
      height: auto;
   }
   @media (min-width: $width-tablet+px) {
      display: block;
   }
   &__body {
      background-color: $color_main;
      display: flex;
      flex-direction: column;
      @media (min-width: $width-tablet+px) {
         background-color: transparent;
         flex-direction: row;
         justify-content: space-between;
      }
   }
   &__list {
      list-style: none;
      box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 2px 0px rgba(255, 255, 255, 0.2);
      @media (min-width: $width-tablet+px) {
         flex: 0 1 20%;
         box-shadow: none;
         &:nth-child(2) {
            margin-right: auto;
         }
      }
      @media (min-width: $width-desktop+px) {
         flex: 0 1 25%;
         &:nth-child(2) {
            margin-right: 0;
         }
      }
   }

   &__link {
      color: $color_white;
      display: inline-block;
      text-decoration: none;
      text-transform: uppercase;
      width: 100%;
      height: 55px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      font-size: 18px;
      line-height: 40px;
      @media (min-width: $width-mobile+px) {
      }
      @media (min-width: $width-tablet+px) {
         width: 75%;
         &--left-align {
            margin-left: auto;
         }
         color: $color_black;
         font-size: 14px;
         line-height: 26px;
      }
      @media (min-width: $width-desktop+px) {
         padding: 0 15px;
         width: 50%;
      }
   }
}
