.form-page {
  &__container {
    padding-top: 64px;
    padding-bottom: 61px;
  }

  &__header {
    text-align: center;
    .features__title {
      font-size: 18px;
      line-height: 26px;
      display: block;
      @media (min-width: $width-tablet+px) {
        font-size: 30px;
        line-height: 26px;
      }
    }
    .features__text {
      display: inline-block;
      max-width: 440px;
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 16px;
      @media (min-width: $width-tablet+px) {
        margin-bottom: 66px;
      }
    }
  }
}
.form {
  display: flex;
  flex-direction: column;
  &__row {
    &--flex {
      display: flex;
      flex-direction: column;
      @media (min-width: $width-tablet+px) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        max-width: 100%;
        align-items: center;
        row-gap: 50px;
      }
      @media (min-width: $width-desktop+px) {
        row-gap: 70px;
      }
    }
  }

  &__impression {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
    order: 2;
    padding: 20px 0 25px 0;
    @media (min-width: $width-tablet+px) {
      flex: 0 1 323px;
      order: 0;
      padding: 0;
    }
    @media (min-width: $width-desktop+px) {
      flex: 0 1 400px;
    }
  }

  &__title {
    text-align: center;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    margin-bottom: 19px;

    @media (min-width: $width-tablet+px) {
      font-size: 21px;
      line-height: 26px;
      margin-bottom: 50px;
      text-align: left;
    }
  }

  &__input-radio {
    display: none;
  }

  &__label {
    position: relative;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 26px;
    color: #333333;
  }

  &__label--radio {
    cursor: pointer;
    font-weight: bold;
    font-size: 18px;
    line-height: 58px;
    height: 58px;
    margin-bottom: 22px;
    text-transform: none;
    padding-left: 78px;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
    &::after {
      content: "";
      position: absolute;
      z-index: 1;
      top: 5px;
      left: 0;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: #f2f2f2;
    }
  }

  &__client-data {
    order: 0;
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
    margin: 0 -20px;
    border: 1px solid #e5e5e5;
    border-bottom: 0;
    border-left: 0;
    border-right: 0;
    padding: 20px 20px 25px;
    @media (min-width: $width-mobile+px) {
      border: 0;
    }
    @media (min-width: $width-tablet+px) {
      flex: 0 1 323px;
      order: 1;
      padding: 0;
      margin: 0;
    }
    @media (min-width: $width-desktop+px) {
      flex: 0 1 400px;
    }
    .form__label {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__client-contact {
    order: 1;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1 100%;
    margin: 0 -20px;
    border: 1px solid #e5e5e5;
    border-left: 0;
    border-right: 0;
    padding: 20px 20px 25px;
    @media (min-width: $width-tablet+px) {
      order: 2;
      padding: 0;
      margin: 0 0 50px;
      border: 0;
    }
    @media (min-width: 800px) {
      column-gap: 60px;
    }
  }

  &__input {
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
    padding: 0 0 0 22px;
    margin-left: 10px;
    background: #f2f2f2;
    border: none;
    outline: none;
    height: 58px;
    width: 100%;
    flex: 0 1 100%;

    &::placeholder {
      font-family: "ptsans", "Arial", sans-serif;
      font-weight: bold;
      font-size: 18px;
      color: $color_black;
      opacity: 1;
      max-width: 100%;
      white-space: nowrap;
      box-sizing: border-box;
    }
  }

  &__title--center {
    width: 100%;
    flex: 1 1 100%;
    text-align: center;
    position: relative;
    overflow-x: hidden;

    @media (min-width: $width-mobile+px) {
      &:after {
        content: "";
        display: block;
        position: absolute;
        z-index: 0;
        top: 50%;
        left: 0;
        width: 123px;
        height: 1px;
        background-color: #dbdbdb;

        @media (min-width: $width-tablet+px) {
          width: 280px;
        }
      }

      &::before {
        content: "";
        display: block;
        position: absolute;
        z-index: 0;
        top: 50%;
        right: 0;
        width: 123px;
        height: 1px;
        background-color: #dbdbdb;

        @media (min-width: $width-tablet+px) {
          width: 280px;
        }
      }
    }
  }

  &__input--comment {
    margin: 0;
    resize: none;
    width: 100%;
    height: 160px;
    padding-top: 13px;
    padding-left: 21px;
  }

  &__btn {
    display: inline-block;
    border: none;
    outline: none;
    height: 58px;
    width: 100%;
    background-color: $color_main;
    color: $color_white;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;
    @media (min-width: $width-mobile+px) {
      &:hover {
        background-color: #669ec0;
      }
      &:active {
        background: #5496bd;
        color: #87b5d1;
      }
    }
    @media (min-width: $width-tablet+px) {
      width: 324px;
    }
    @media (min-width: $width-desktop+px) {
      width: 480px;
    }
  }

  &__req-text {
    display: none;
    @media (min-width: $width-tablet+px) {
      display: inline-block;
      font-size: 14px;
      line-height: 26px;
      text-transform: uppercase;
      color: #333333;
    }
  }
}
.contact-field {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex: 1 1 100%;
  margin-bottom: 14px;
  @media (min-width: $width-mobile+px) {
    margin-bottom: 25px;
  }
  @media (min-width: $width-tablet+px) {
    flex: 1 1 auto;
  }
  @media (min-width: 800px) {
    margin-bottom: 0;
  }

  &__label {
    flex: 1 1 100%;
    text-transform: uppercase;
    color: #333333;
    font-size: 14px;
    line-height: 26px;
    margin-bottom: 8px;
    margin-left: 24px;
  }
  &:last-child {
    margin-bottom: 0;
  }

  &__input-wrapper {
    width: 100%;
  }

  &__input {
    margin: 0;
  }
}

.form__row-monuments {
  margin: 0 -20px;
  padding: 15px 20px 30px;
  border: 1px solid #e5e5e5;
  border-left: 0;
  border-right: 0;
  display: flex;
  flex-wrap: wrap;
  @media (min-width: $width-tablet+px) {
    border: 0;
    padding: 0;
    margin: 0px 0px 50px;
    border: 0;
  }
  @media (min-width: $width-desktop+px) {
    padding: 0;
    margin-bottom: 70px;
  }
  .form__input-checkbox {
    display: none;
  }

  .form__label--checkbox {
    cursor: pointer;
    flex: 1 1 100%;
    padding-left: 80px;
    font-weight: bold;
    font-size: 18px;
    line-height: 56px;
    position: relative;
    margin-bottom: 24px;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
    @media (min-width: $width-tablet+px) and (max-width: $width-desktop+px) {
      flex: 1 1 50%;
    }
    @media (min-width: $width-desktop+px) {
      flex: 1 1 25%;
    }
    &:after {
      content: "";
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 54px;
      height: 54px;
      border: 1px solid #f2f2f2;
      background: #f2f2f2;
      border-radius: 5px;
    }
  }
}

.form__row-comments {
  padding: 20px 0 51px 0;
  @media (min-width: $width-tablet+px) {
    padding: 0 0 58px;
  }
}

.form__input-checkbox:checked + .form__label--checkbox::before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  background: url("../img/icon-tick.svg") center no-repeat;
}
.form__input-radio:checked + .form__label--radio::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  background-color: $color_black;
  z-index: 2;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  border-radius: 50%;
}
