$max-width: 1200px;
$width-desktop: 1200;
$width-tablet: 767.98;
$width-mobile: 375.98;
.container {
   box-sizing: border-box;
   width: 100%;
   height: 100%;
   padding-left: 20px;
   padding-right: 20px;
   margin: 0 auto;
   
   @media (min-width: $width-tablet+px) {
      padding-left: 60px;
      padding-right: 60px;
      max-width: 100%;
   }
   @media (min-width: $width-desktop+px) {
      max-width: $max-width;
      margin: 0 auto;
      padding-left: 95px;
      padding-right: 95px;
   }
}
.container-fluid {
   min-width: 320px;
   width: 100%;
   max-width: $max-width;
   margin: 0 auto;
}
