.findhotel {
}
.findhotel__container {
   padding-top: 33px;
   padding-bottom: 49px;
   text-align: center;
   @media (min-width: $width-tablet+px) {
      padding-top: 57px;
      padding-bottom: 58px;
   }
   @media (min-width: $width-desktop+px) {
      padding-bottom: 66px;
   }
}

.findhotel__title {
   text-align: center;
   text-transform: uppercase;
   font-weight: bold;
   font-size: 21px;
   line-height: 26px;
   margin-bottom: 32px;
   @media (min-width: $width-tablet+px) {
      margin-bottom: 37px;
      font-size: 30px;
      line-height: 26px;
   }
}
.findhotel__text {
   text-align: center;
   font-size: 14px;
   line-height: 24px;
   text-transform: uppercase;
   display: inline-block;
   width: 260px;
   margin: 0 auto 27px;
   @media (min-width: $width-tablet+px) {
      margin-bottom: 37px;
      width: 435px;
   }
   @media (min-width: $width-desktop+px) {
      margin-bottom: 42px;
   }
}
.findhotel__link {
   text-decoration: none;
   text-transform: uppercase;
   display: inline-block;
   height: 58px;
   width: 100%;
   font-weight: bold;
   font-size: 21px;
   line-height: 26px;
   color: $color_white;
   background-color: $color_main;
   display: flex;
   align-items: center;
   justify-content: center;
   margin: 0 auto;
   @media (min-width: $width-mobile+px) {
      &:hover {
         background-color: #669ec0;
      }
      &:active {
         background: #5496bd;
         color: #87b5d1;
      }
   }
   @media (min-width: $width-tablet+px) {
      width: 458px;
   }
}
