.promo {
   position: relative;
   &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 57px;
      background-image: url("../img/bg-intro-triangle-mobile.svg");
      background-repeat: no-repeat;
      background-position: bottom center;
      background-size: cover;
      @media (min-width: $width-tablet+px) {
         background-image: url("../img/bg-intro-triangle-tablet.svg");
      }
      @media (min-width: 1200px) {
         background-image: url("../img/bg-intro-triangle-desktop.svg");
         background-size: 100% 57px;
      }
   }
}

.promo__bgimg {
   padding-top: 90px;
   width: 100%;
   height: 389px;
   background: url("../img/bg-back-mobile@1x.jpg") center no-repeat;
   background-size: cover;
   @media (min-width: $width-tablet+px) {
      height: 518px;
      padding-top: 77px;
      background: url("../img/bg-back-tablet@1x.jpg") no-repeat;
      background-size: cover;
   }
   @media (min-width: $width-desktop+px) {
      padding-top: 78px;
      background: url("../img/bg-back-desktop@1x.jpg") no-repeat;
      background-size: cover;
   }
   &--form-page {
      height: 158px;
      padding-top: 0;
      background-position: center bottom;
      @media (min-width: $width-tablet+px) {
         height: 290px;
      }
   }
}

.promo__content {
   width: 282px;
   height: 214px;
   margin: 0 auto;
   img {
      width: 100%;
      height: 100%;
   }
   @media (min-width: $width-tablet+px) {
      width: 461px;
      height: 350px;
   }
   @media (min-width: $width-desktop+px) {
      width: 460px;
      height: 350px;
   }
   &--form-page{
      height: 100%;
      display: flex;
      align-items: center;

   }
}
