.footer {
  &__container {
    padding: 38px 0px 26px 0px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    @media (min-width: $width-tablet+px) {
      padding: 36px 0;
      flex-direction: row;
      align-items: center;
      width: 100%;
      justify-content: space-around;
    }
  }

  &__sedona-link {
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;
    text-transform: uppercase;
    color: $color_black;
    text-decoration: none;
    margin-bottom: 33px;
    @media (min-width: $width-tablet+px) {
      margin: 0;
    }
  }

  &__social {
    margin-bottom: 45px;
    @media (min-width: $width-tablet+px) {
      margin: 0;
    }
  }
  &__academy-link {
    font-size: 14px;
    line-height: 26px;
    text-decoration: none;
    text-transform: uppercase;
    color: $color_black;
    display: flex;
    align-items: center;
    justify-content: center;
    fill: $color_black;
    &:hover {
      fill: $color_main;
    }
    &:active {
      fill: #b3b3b3;
    }
  }
  &__academy-text {
    margin-right: 9px;
  }
}
.footer-social {
  display: flex;
  justify-content: space-around;
  gap: 6px;
  &__link {
    width: 46px;
    height: 48px;
    background-color: $color_main;
    display: flex;
    align-items: center;
    justify-content: center;
    fill: $color_white;
    &:hover {
      background: #669ec0;
    }
    &:active {
      fill: rgba($color: white, $alpha: 0.5);
    }
  }
}
