.features {
   &__container {
      padding-top: 32px;
      text-align: center;
      @media (min-width: $width-tablet+px) {
         padding-top: 43.5px;
      }
      @media (min-width: $width-desktop+px) {
         padding-top: 55px;
      }
   }
   &__title {
      text-transform: uppercase;
      font-weight: bold;
      font-size: 18px;
      line-height: 26px;
      display: inline-block;
      width: 100%;
      margin: 0 auto 40px;
      text-align: center;
      @media (min-width: $width-tablet+px) {
         font-size: 21px;
         line-height: 26px;
         width: 520px;
         br {
            display: none;
         }
      }
      @media (min-width: $width-desktop+px) {
         font-size: 21px;
         line-height: 26px;
         width: 484px;
      }
   }

   &__text {
      font-size: 18px;
      line-height: 26px;
      text-align: center;
      margin-bottom: 38px;
      text-transform: uppercase;
      @media (min-width: $width-tablet+px) {
         margin-bottom: 52px;
      }
      @media (min-width: $width-desktop+px) {
         margin-bottom: 41px;
      }
   }
}
.features-grid {
   margin: 0 -20px;
   display: grid;
   grid-template-columns: 1fr;
   @media (min-width: $width-tablet+px) {
      margin: 0 -32px;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: minmax(256px, auto) minmax(330px, 1fr) repeat(2, minmax(256px, auto));
   }
}
.features-grid__item {
   padding: 0 20px;
}

.features-grid__item--text {
   padding-top: 35px;
   padding-bottom: 35px;
   background-color: $color_main;
   text-align: center;
}

.features-grid__item-title {
   text-transform: uppercase;
   font-weight: bold;
   font-size: 18px;
   line-height: 26px;
   margin-bottom: 20px;
   br {
      display: none;
   }
   @media (min-width: $width-tablet+px) {
      font-size: 21px;
      line-height: 26px;
      br {
         display: inline-block;
      }
   }
}

.features-grid__item-title--white {
   color: $color_white;
}

.features-grid__item-p {
   text-align: center;
   font-weight: normal;
   text-transform: uppercase;
   font-size: 14px;
   line-height: 21px;

   &--mbot {
      margin-bottom: 25px;

      @media (min-width: $width-tablet+px) {
         margin-bottom: 30px;
      }
   }
}

.features-grid__item-p--white {
   color: $color_white;
}

.features-grid__item--img {
   padding: 0;
   justify-self: center;
   height: 256px;
   width: 100%;
   position: relative;
   img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
   }
   @media (min-width: $width-tablet+px) {
      justify-self: stretch;
      max-height: 100%;

      &-1 {
         grid-column: 2 / span 2;
      }
      &-2 {
         grid-column: 1 / span 2;
         grid-row: 3;
      }
   }
}

.features-grid__item--svg {
   padding-left: 69px;
   padding: 15px 0px 10px 69px;
   position: relative;
   svg {
      fill: $color_main;
      position: absolute;
      top: 15px;
      left: 18px;
      width: 40px;
      height: 40px;
   }
   .features-grid__item-title {
      text-align: left;
      @media (min-width: $width-tablet+px) {
         text-align: center;
      }
   }
   @media (min-width: $width-tablet+px) {
      padding: 58px 30px;
      svg {
         position: relative;
         top: 0;
         left: 0;
         width: 75px;
         height: 75px;

         margin-bottom: 35px;
      }
   }

   .features-grid__item-p {
      text-align: left;
      @media (min-width: $width-tablet+px) {
         text-align: center;
      }
   }
}

.features-grid__item--bggray {
   background: #eeeeee;
   border-bottom: 1px solid #d6d6d6;
   &:last-child {
      border-bottom: 0;
   }
   @media (min-width: $width-tablet+px) {
      padding-left: 35px;
      padding-right: 35px;
   }
}
